@import "../../../Common/commonStyle.less";

.form-box {
    margin-top: 5%;
    width: 23.75vw;
    padding: 30px;
    background: rgba(255, 255, 255, 1);
    box-shadow: 0px 0px 16px 2px rgba(0, 0, 0, 0.2);
    border-radius: 10px;
}

.login-box {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translateY(-50%);
    text-align: center;

    .ant-form-item {
        margin-bottom: 20px !important;
    }


    &-title {
        color: rgba(255, 255, 255, 1);
        font-family: @PingFang-SC-Bold-fontFamily;
        font-weight: bold;
    }

    .login-form {
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;
        background-image: url('../../../Assets/account/login-bg@2x.png');
        background-size: 100% 100%;
        background-repeat: no-repeat;

        &-title {
            font-family: @PingFang-SC-Medium-fontFamily;
            font-weight: 500;
            color: rgba(255, 255, 255, 1);
        }

        &-account {
            border-radius: 4px;

            .ant-input-group-addon .ant-select.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {

                background-color: #fff;
            }

            .ant-input-group-addon {
                border-color: #fff !important;
                padding-right: 0 !important;
                overflow: hidden;
            }

            .ant-select-selector {
                padding: 0 !important;

            }

            .ant-input {
                border-left: none !important;
                border-color: #fff !important;

                &:hover {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }

                &:focus {
                    border: 1px solid #fff !important;
                    border-left: none !important;
                    box-shadow: none !important;
                }
            }
        }

        .login-btn {
            position: relative;
            background-color: rgba(255, 255, 255, 0);
            border: none;
            background-image: url(../../../Assets/account/btn-border@2x.png);
            background-size: 100% 100%;
            outline: none;
            text-decoration: none;
            color: #fff;
            overflow: hidden;
            cursor: pointer;

            img {
                position: absolute;
                left: 0;
                top: 0;
                width: 60%;
                height: 100%;
                animation: loginBtnBg 3s linear infinite alternate;
            }
        }

        .login-mode {
            p {
                font-family: @PingFang-SC-Medium-fontFamily;
                color: rgba(255, 255, 255, 1);
            }

            div {
                cursor: pointer;

                span {
                    font-family: @PingFangSC-Regular-fontFamily;
                    color: rgba(100, 223, 222, 1);
                }
            }
        }
    }
}

.animate-route {
    animation-duration: 1s;
    animation-fill-mode: both;

    animation: fadeRoute .2s linear;
}

@keyframes fadeRoute {
    from {
        left: -100%;
        opacity: 0;
    }

    to {
        left: 0;
        opacity: 1;
    }
}


@media screen and(min-width: 1025px)and(max-width: 1440px) {
    .login-box {
        margin-left: -180px;

        &-title {
            font-size: 27px;
            line-height: 38px;
            text-shadow: 0px 2px 17px rgba(19, 21, 44, 0.52);
        }

        .login-form {
            width: 360px;

            &-title {
                padding-top: 50px;
                font-size: 20px;
                color: rgba(255, 255, 255, 1);
                line-height: 28px;
                margin-bottom: 25px;
            }

            .login-btn {
                width: 236px;
                height: 35px;
            }
            &-account {
                .ant-input-group-addon {
                    padding: 3px !important;

                }
            }

            .login-mode {
                p {
                    font-size: 12px;
                    font-weight: 500;
                    line-height: 17px;

                }

                div {
                    span {
                        font-size: 11px;
                        font-weight: 400;
                        line-height: 15px;
                        position: relative;
                        left: -4px;

                    }

                    .wechat {
                        position: relative;
                        left: -2px;
                        width: 36px;
                        height: 36px;
                    }

                }

            }

        }
    }
}

@media screen and(min-width: 1441px)and(max-width: 1920px) {
    .login-box {
        margin-left: -240px;

        &-title {

            font-size: 36px;
            line-height: 50px;
            text-shadow: 0px 2px 22px rgba(19, 21, 44, 0.52);

        }

        .login-form {
            width: 480px;

            &-title {
                padding-top: 50px;
                font-size: 20px;
                color: rgba(255, 255, 255, 1);
                line-height: 28px;
                margin-bottom: 25px;
            }

            &-account {
                .ant-input {
                    font-size: 16px;
                    padding: 10px;
                }

                .ant-input-group-addon {
                    padding: 10px !important;
                    background-color: #fff;

                }
            }

            .login-btn {
                width: 315px;
                height: 50px;
            }

            .login-mode {
                p {

                    font-size: 16px;
                    line-height: 22px;
                    font-weight: 500;

                }

                div {
                    span {
                        position: relative;
                        left: -2px;
                        font-size: 14px;
                        font-weight: 400;
                        line-height: 20px;

                    }

                    .wechat {
                        position: relative;
                        left: -8px;
                        width: 42px;
                        height: 42px;
                    }

                }

            }

        }

    }
}



@keyframes loginBtnBg {
    0% {
        left: 0%;
        opacity: 0;
    }

    33% {
        // transform: scale(1.1, 1.1);
        left: 13%;
        opacity: .8;
    }

    66% {
        // transform: scale(1.3, 1.3);
        left: 29%;
        opacity: 1;
    }

    100% {
        // transform: scale(1.1, 1.1);
        left: 40%;
        opacity: 0;
    }
}